import React,{Component} from 'react'
import { Card,Button } from 'antd';
import axios from 'axios'

export default class Rpacket extends Component{
	state={
		list:[]
	}
	render(){
		return(
			<div style={{ background: '#ECECEC', padding: '30px' }}>
			    <Card title='我的红包' bordered={false} style={{ width: "100%", height: "560px",overflow:'hidden'}}>
				{
					this.state.list.map((item,i)=>{
						return(
							<Card title="分享红包" extra={<a href="#">More</a>} style={{ width: 400,height:218,float:'left',margin:'0 20px 20px 0'}}>
								<span style={{fontSize:40,color:'red',float:"right",fontWeight:"bold",marginTop:'-18PX'}}><span style={{fontSize:20,color:'#999',fontWeight:'normal	'}}>￥</span>{item.sum_condition}</span>
								<span style={{color:'blue'}}>限{item.phone}使用</span><br/>
								<span style={{color:'orange'}}>满20元可用</span><br/>
								<span style={{color:'red'}}>使用时间：{item.begin_date}至{item.end_date}</span>
								<Button type="primary" style={{float:"right",margin:"0px -84px 0px"}}>立即使用</Button>
								<br/><br/><hr style={{color:'#ccc',width:'100%'}}/><br/>
								<p style={{textAlign:"center"}}>限在线支付使用</p>
							</Card>
						)
					})
				}	
				
			   
			    </Card>
			  </div>
		)
	}
	componentDidMount(){
		axios({
			url:'https://elm.cangdu.org/promotion/v2/users/1/hongbaos?limit=20&offset=0',
		}).then((data)=>{
			console.log(data.data)
			this.setState({list:data.data})
		})
	}
}