import React,{Component} from 'react'
import { Card,Popover, Button,Divider,Descriptions } from 'antd';
export default class Balance extends Component{
	
	render(){
		const content = (
		  <div>
		    <p>暂不支持提现,请使用余额消费</p>
		  </div>
		)
		return(
			<div>
				<Card size="small" title="余额"  style={{ width: "100%",height:'40px',overflow:'hidden' }}></Card>
				<Divider />
				<Card>
				 当前账户余额: <span style={{color:"red"}}> 0 </span>元
				 <Popover content={content} title="提示" style={{marginLeft:'2px'}}>
				    <Button type="primary">提现</Button>
				  </Popover>
				</Card>
				<Descriptions
				        title="Responsive Descriptions"
				        bordered
				        column={{ xxl: 4, xl: 3, lg: 3, md: 3, sm: 2, xs: 1 }}
				      >
				        <Descriptions.Item label="Product">Cloud Database</Descriptions.Item>
				        <Descriptions.Item label="Billing">Prepaid</Descriptions.Item>
				        <Descriptions.Item label="time">18:00:00</Descriptions.Item>
				        <Descriptions.Item label="Amount">$80.00</Descriptions.Item>
				        <Descriptions.Item label="Discount">$20.00</Descriptions.Item>
				        <Descriptions.Item label="Official">$60.00</Descriptions.Item>
				        <Descriptions.Item label="Config Info">
				          Data disk type: MongoDB
				          <br />
				          Database version: 3.4
				          <br />
				          Package: dds.mongo.mid
				          <br />
				          Storage space: 10 GB
				          <br />
				          Replication_factor:3
				          <br />
				          Region: East China 1
				        </Descriptions.Item>
				</Descriptions>
			</div>
		)
	}
}