import React,{Component} from 'react'
import { Tabs, Radio,Card,Button  } from 'antd';
import api from '../../api/api'
const { TabPane } = Tabs;
export default  class Detail extends Component{
    constructor(props){
        super(props)
        this.state={
            detail:[],
            mode: 'top',
            
        }
    }
    handleModeChange = e => {
        const mode = e.target.value;
        this.setState({ mode });
      };
    render() {
        const { mode } = this.state;
        const gridStyle = {
            width: '25%',
            textAlign: 'center',
          };
          
        return(
        
            <div>
                <Radio.Group onChange={this.handleModeChange} value={mode} style={{ marginBottom: 8 }}>
                <Radio.Button value="top">Horizontal</Radio.Button>
                <Radio.Button value="left">Vertical</Radio.Button>
                </Radio.Group>
                
                <Tabs defaultActiveKey="1" tabPosition={mode} style={{ overflow:'auto' }}>
                {
                   
                    this.state.detail.map((item,i)=>{
                        return(
                            <TabPane tab={item.name} key={i}>
                                <div style={{ background: '#ECECEC', padding: '30px',display:'flex',justifyContent:'space-around',flexWrap:"wrap"  }}>
                                    {/* <Card title="Card title" bordered={false} style={{ width: 300,margin:'5px 0',position:'relative' }}>
                                      <p>Card content</p>
                                      <p>Card content</p>
                                      <p>Card content</p>
                                    </Card> */}
                                    {
                                        item.foods.map((items,j)=>{
                                            return(
                                                
                                                <Card title={items.name} key={j} bordered={false} style={{ width: 300,margin:'5px 0',position:'relative' }}>
                                                    <p><img src={'http://elm.cangdu.org/img/'+items.image_path} style={{width:68}}/></p>
                                                    <p style={{    position: "absolute",top: "87px",left: "116px",color: "orange",fontSize: "1px"}}><span>{items.description}</span></p>
                                                    <p style={{    position: "absolute",right: 24,top: 125,fontSize: 12}}><span>月售：{items.month_sales}份</span></p>
                                                    <p style={{    position: "absolute",right: 120,top: 125,fontSize: 12}}><span>好评率{items.satisfy_rate}%</span></p>
                                                    <p style={{    position: "absolute",right: "52px",top: "86px",fontSize: "10px",border:" 1px solid orange",borderRadius: "8px"}}>招牌</p>
                                                    <p><Button type="primary" onClick={()=>this.add(i,j)}>加入购物车</Button></p>
                                                     <p style={{position:'absolute',right:15,bottom:27}}>
                                                        <input type='number' min='1' ref='num'  style={{    backgroundColor:" #fff",width: "36px",left: "27px",border: "1px solid #ccc",textAlign:'center'}}/>
                                                     </p> 
                                                     <p  style={{position:'absolute',right:17,top:18,color: "red"}}>￥{items.specfoods[0].price}元</p>
                                                </Card>
                                            )
                                        })
                                    }
                                  
                                </div>
                            </TabPane> 
                        )
                    })
                }
                </Tabs>
            </div>
        )   
    }
    add(i,j){
        console.log(i,j)
        console.log(this.state.detail[i].foods[j])
        console.log(this.props)
        let num = 1
        num = this.refs.num.value
        if(num==''){
            num=1
        }
        let id = this.state.detail[i].foods[j]._id
        let product = this.state.detail[i].foods[j]
        let jsonstr = localStorage.getItem('carts')
			if(jsonstr){
				let arr = JSON.parse(jsonstr)
				let pObj = arr.find(v=>v._id==id)
				if(pObj){
					pObj.num++
				}else{
					arr.push(product)
				}
                localStorage.setItem('carts',JSON.stringify(arr))
                this.props.history.push('/detail')
			}else{
				let arr = []
				//第一次添加 num=1
				product.num = 1
				arr.push(product)
				localStorage.setItem('carts',JSON.stringify(arr))
			}
    }
    
    componentDidMount(){
        console.log(this.props.location.query.id)
        api.getShopMenu({restaurant_id:this.props.location.query.id}).then((data)=>{
            console.log(data)
            this.setState({
                detail:data
            })
        })
    }
}