import React,{Component} from 'react'
import { Menu, Icon, Switch } from 'antd';
import {HashRouter as Router,Route,NavLink,Redirect} from 'react-router-dom'

import Person from './components/Person.js'
import Oldorder from './components/Oldorder.js'
import Rpacket from './components/Rpacket.js'
import Mymoney from './components/Mymoney.js'
import Balance from './components/Balance.js'
import PerData from './components/PerData.js'
import Address from './components/Address.js'
import MyFavorite from './components/MyFavorite.js'
const { SubMenu } = Menu;


 
export default class Mypro extends Component{
	state = {
	    theme: 'dark',
	    current: '1',
	  };
	
	  changeTheme = value => {
	    this.setState({
	      theme: value ? 'dark' : 'light',
	    });
	  };
	
	  handleClick = e => {
	    console.log('click ', e);
	    this.setState({
	      current: e.key,
	    });
	  };
	render(){
		return(
		
			 <div>
			       
			        <Menu
			          theme={this.state.theme}
			          onClick={this.handleClick}
			          style={{ width: 256 }}
			          defaultOpenKeys={['sub1','sub2','sub3']}
			          selectedKeys={[this.state.current]}
			          mode="inline"
			        >
					  <Menu.Item key="1">
							<Icon type="home" />
							<span><NavLink to='/home/mypro/person' style={{color:'white'}}>个人中心</NavLink></span>
						</Menu.Item>
			          <SubMenu
			            key="sub1"
			            title={
			              <span>
			                <Icon type="mail" />
			                <span>我的订单</span>
			              </span>
			            }
			          >
			            <Menu.Item key="2"><NavLink to='/home/mypro/oldorder'>近三个月订单</NavLink></Menu.Item>
			          </SubMenu>
			          <SubMenu
			            key="sub2"
			            title={
			              <span>
			                <Icon type="appstore" />
			                <span>我的资产</span>
			              </span>
			            }
			          >
			            <Menu.Item key="3"><NavLink to='/home/mypro/rpacket'>我的红包</NavLink></Menu.Item>
			            <Menu.Item key="4"><NavLink to='/home/mypro/balance'>账户余额</NavLink></Menu.Item>
						<Menu.Item key="5"><NavLink to='/home/mypro/mymoney'>我的金币</NavLink></Menu.Item>
			          </SubMenu>
			          <SubMenu
			            key="sub3"
			            title={
			              <span>
			                <Icon type="setting" />
			                <span>我的资料</span>
			              </span>
			            }
			          >
			            <Menu.Item key="6"><NavLink to='/home/mypro/perdata'>个人资料</NavLink></Menu.Item>
			            <Menu.Item key="7"><NavLink to='/home/mypro/address'>地址管理</NavLink></Menu.Item>
			          </SubMenu>
					  <Menu.Item key="8">
						<Icon type="pie-chart" />
						<span>我的收藏</span>
					</Menu.Item>
			        </Menu>
						<div style={{width:"1120px",height:"525px",position:'absolute',right: "68px",top: "220px"}}>
						<Router>
							<Route path='/home/mypro/person' component={Person}></Route>
							<Route path='/home/mypro/oldorder' component={Oldorder}></Route>
							<Route path='/home/mypro/rpacket' component={Rpacket}></Route>
							<Route path='/home/mypro/mymoney' component={Mymoney}></Route>
							<Route path='/home/mypro/balance' component={Balance}></Route>
							<Route path='/home/mypro/perdata' component={PerData}></Route>
							<Route path='/home/mypro/address' component={Address}></Route>
							<Route path='/home/mypro/myfavorite' component={MyFavorite}></Route>
							<Redirect to='/home/mypro/person' />
				     </Router>
							
						</div>
			      </div>
		)
	}
}