import React,{Component} from 'react'
import { Tabs, Radio,Card,Badge,Modal,Rate   } from 'antd';
import {NavLink} from 'react-router-dom'
import api from '../../api/api'
import Item from 'antd/lib/list/Item';
const { TabPane } = Tabs;
export default class Index extends Component{
	constructor(props) {
		super(props);
		this.state = {
		  mode: 'top',
		  list:[],
		  visible: false,
		  longitude:'',
		  latitude:'',
		  showList:[]
		  
		};
	  }
	  showModal = () => {
		this.setState({
		  visible: true,
		});
	  };
	
	  handleOk = e => {
		console.log(e);
		this.setState({
		  visible: false,
		});
	  };
	
	  handleCancel = e => {
		console.log(e);
		this.setState({
		  visible: false,
		});
	  };
	  handleModeChange = e => {
		const mode = e.target.value;
		this.setState({ mode });
	  };
	  componentDidMount(){
		api.getShopCategory().then((data)=>{
			console.log(data)
			this.setState({list:data})
		})
		// 获取定位城市经纬度
		let params={
			type:"guess"
		}
		api.getCity(params).then((data)=>{
			// console.log(data.latitude)
			// 保存到state总
			// console.log(data.longitude)
			this.setState({
				longitude:data.longitude,
				latitude:data.latitude
			})
		})
		//根据经纬度获取附近商家信息
		let params1={
			longitude:113.624931,
			latitude:34.74725
		}
		api.getShopList(params1).then((data)=>{
			console.log(data)
			this.setState({
				showList:data
			})
		})
	  }
	render(){
		const gridStyle = {
			width: '25%',
			textAlign: 'center',
		  };
		return(
			<div>
		        <Radio.Group onChange={this.handleModeChange} value={this.state.mode} style={{ marginBottom: 8 }}>
		          <Radio.Button value="top">Horizontal</Radio.Button>
		          <Radio.Button value="left">Vertical</Radio.Button>
		        </Radio.Group>
		        <Tabs defaultActiveKey="8" tabPosition={this.state.mode} style={{overflow:"auto",marginBottom:'5px' }} >
		          {/* {[...Array(30).keys()].map(i => (
		            <TabPane tab={`Tab-${i}`} key={i}>
		              Content of tab {i}
		            </TabPane>
		          ))} */}
		          {
					  this.state.list.map((item,i)=>{
						return(
							<TabPane tab={item.name} key={i}>
				              {
				                item.sub_categories.map((items,j)=>{
									return(
										// <div style={{float:'left',textAlign: "center",padding: "0px 30px",border:'1px solid #ccc',overflow:'auto'}} 
										// 	key={j} >
										// 	<p>{items.name}</p>
										// 	<img src={"https://fuss10.elemecdn.com/"+items.image_url+".png"}/>
										// </div>
											<div>
											<Card.Grid key={items.id} style={gridStyle} onClick={this.showModal}>{items.name}<Badge count={items.count} /></Card.Grid>
											<Modal
											title="Basic Modal"
											visible={this.state.visible}
											onOk={this.handleOk}
											onCancel={this.handleCancel}
											>
											<p>Some contents...</p>
											<p>Some contents...</p>
											<p>Some contents...</p>
											</Modal>
											</div>
									)
								})
				              }
				              
				            </TabPane>
				          
						)
					  })
					}
		        </Tabs>
		        <div style={{ background: '#ECECEC', padding: '30px',display:'flex',justifyContent:'space-around',flexWrap:"wrap" }}>
					
					{
						this.state.showList.map((item,i)=>{
							return(
								<Card title={item.name} key={i}  bordered={false} style={{ width: 300,margin:'5px 0',position:'relative' }}>
									<NavLink to={{pathname:'/home/detail',query:{id:item.id}}}><p><img src={'//elm.cangdu.org/img/'+item.image_path} style={{width:68}}/></p>
									<p style={{color:'#57A9FF',position:'absolute',right: "60px",top: "75px"}}><span style={{fontSize:12}}>{item.delivery_mode.text}</span></p>
									<p style={{color:'#57A9FF',position:'absolute',right: "20px",top: "75px"}}><span style={{fontSize:12}}>准时达</span></p>
									<p style={{color:'#000'}}> <span>{item.float_minimum_order_amount}元起送/</span><span>{item.piecewise_agent_fee.tips}</span></p>
									<p><span>{item.promotion_info}</span></p>
									<p style={{color:'#57A9FF',position:'absolute',right: "26px",top: "120px"}}><span style={{fontSize:12}}>距离{item.distance}</span></p>
									<p style={{color:'#000',position:'absolute',right: "5px",top: "160px"}}><span style={{fontSize:12}}>预计：{item.order_lead_time}</span></p>
									<p style={{color:'#000',position:'absolute',right: "133px",top: "130px"}}><span style={{fontSize:12}}>月售：{item.recent_order_num}单</span></p>
									<span style={{position:'absolute',right: "118px",top: "102px"}}><Rate disabled defaultValue={item.rating} style={{fontSize: "3px"}}/></span>
									</NavLink>
								</Card>
							)
						})
					}
					
					
				</div>
            </div>
		)
	}
	
	
}