import axios from 'axios'
import NProgress from 'nprogress';
import 'nprogress/nprogress.css'
const baseUrl = 'https://elm.cangdu.org'
const baseUrl1 ='http://jx.xuzhixiang.top/ap/api'
// 添加请求拦截器
axios.interceptors.request.use(function (config) {
    // 在发送请求之前做些什么
    NProgress.start()
    return config;
}, function (error) {
    // 对请求错误做些什么
    return Promise.reject(error);
});

// 添加响应拦截器
axios.interceptors.response.use(function (response) {
    // 对响应数据做点什么
    NProgress.done()
    return response;
}, function (error) {
    // 对响应错误做点什么
    return Promise.reject(error);
});


export const GET=(url,params)=>{
    return axios.get(`${baseUrl}${url}`,{params:params}).then(res=>res.data)
}

export const POST=(url,params)=>{
    return axios.post(`${baseUrl}${url}`,params).then(res=>res.data)
}

// 登录接口
export const GET1=(url,params)=>{
    return axios.get(`${baseUrl1}${url}`,{params:params}).then(res=>res.data)
}

export const POST1=(url,params)=>{
    return axios.post(`${baseUrl1}${url}`,params).then(res=>res.data)
}