import React, { Component } from 'react';
import { List, Avatar, Icon,Button  } from 'antd';
import { red } from 'ansi-colors';

const listData = [];
const list=localStorage.getItem('carts')
let arr = JSON.parse(list)
if (arr!= null) {
    arr.forEach(v=>{
        listData.push({
            Shopname:`${v.name}`,
            pingjia:`${v.tips}`,
            imgpath:`${v.image_path}`,
            price:`${v.specfoods[0].price}`,
            num:`${v.num}`,
            description:`${v.description}`
        })
    })
}else{
    for (let i = 0; i < 1; i++) {
        listData.push({
            description:
                '购物车为空',
            
        });
      }
}



const IconText = ({ type, text }) => (
    <span>
        <Icon type={type} style={{ marginRight: 8 }} />
        {text}
    </span>
);

class Cart extends Component {
    constructor(props){
        super(props)
        this.state={
            money:'0'
        }
    }
    
    delete(){
        console.log('清空购物车')
        localStorage.clear()
    }
    
    render() {
        // console.log(list)
        return (
            <List
                itemLayout="vertical"
                size="large"
                style={{padding:80,
                    margin: "60px",
                    padding: "10px",
                    border: "1px solid black",
                    borderRadius: "5px",
                    boxShadow: "0px 0px 21px 7px #cecece",
                   
                }}
                pagination={{
                    onChange: page => {
                        console.log(page);
                    },
                    pageSize: 3,
                }}
                dataSource={listData}
                header={
                    <div>
                        <b>购物车</b> 
                    </div>
                }
                footer={
                    <div>
                        <b>  总金额：{this.state.money}
                            <Button style={{
                                    position: "absolute",
                                    right: 20
                            }} type='danger' onClick={this.delete.bind(this)}>清空购物车</Button>
                        </b> 
                    </div>
                }
                renderItem={(item,i)=> (
                    <List.Item
                        style={{ position:"relative"}}
                        key={i}
                        actions={[
                            <IconText type="star-o" text="156" key="list-vertical-star-o" />,
                            <IconText type="like-o" text="156" key="list-vertical-like-o" />,
                            <IconText type="message" text="2" key="list-vertical-message" />,
                          ]}
                        extra={
                            <img
                                width={100}
                                alt="logo"
                                src={'http://elm.cangdu.org/img/'+item.imgpath}
                            />
                        }
                    >
                        <List.Item.Meta
                            title={<p>{item.Shopname}</p>}
                            description={<p>{item.description}</p>}
                            
                        />
                        {<input type='number' style={{
                            color: "green",
                            width: "45px",
                            borderRadius: "9px",
                            outline: "aliceblue",
                            border: "1px solid #ccc",
                            cursor: "pointer",
                            textAlign: "center",
                        }} value={item.num} />}
                       <div style={{
                            position: "absolute",
                            top: "90px",
                            right: "195px",
                            fontSize: "18px",
                            color: "green",
                           
                        }}>单价<Icon type="property-safety" /> ${item.price}元</div>
                        {<p style={{
                                position: "absolute",
                                left: "100px",
                                top: "58px",
                                border:" 1px solid #ccc",
                                borderRadius: "5px",
                                background: "red",
                                color: "wheat",
                        }}>{item.pingjia}</p>}
                    </List.Item>
                )}
            />
        );
    }
}

export default Cart;