import React,{Component} from 'react'
import {HashRouter as Router,NavLink,Route,Redirect,Switch} from 'react-router-dom'
import './Home.css'
import styles from './Home.css'
import { Layout, Menu, Breadcrumb,Icon,Input,Modal, Button,Popover } from 'antd';
import Service from '../Service/Service.js'
import Index from '../Index/Index.js'
import Question from '../Question/Question.js'
import Phone from '../Phone/Phone.js'
import Mypro from '../Mypro/Mypro.js'
import Detail from '../Detail/Detail.js'
import img from './2.png'
import axios from 'axios';
// import api from '../api/api.js'
const { Search } = Input;
const { Header, Content, Footer } = Layout;
const content = (
	<div>
	  <p><img className="erweima" src={require('./erweima.png')}  style={{width:120,height:120}}/></p>
	</div>
  );

export default class Home extends Component{
	login(){
		this.props.history.push('/login')
}
register(){
this.props.history.push('/register')
}
	Cart(){
		this.props.history.push('/cart')
	}
	constructor(props){
			super(props)
			this.state = {
			    width: 20,
				rotate:0,
				address:'',
				index:[],
				city:[],
				visible: false
			  }

			 
		}
		
		showModal = () => {
			this.setState({
			  visible: true,
			});
		  };

		  handleOk = e => {
			console.log(e);
			this.setState({
			  visible: false,
			});
		  };
		
		  handleCancel = e => {
			console.log(e);
			this.setState({
			  visible: false,
			});
		  };
		  
		 
	    toggle () {
			if(this.state.width==20){
				this.setState({
				  width: 260,
				  rotate:-180
				});
			}else{
				this.setState({
				  width: 20,
				  rotate:360
				});
			}
	        
	      };
	render(){
		return(
		
			
			<Layout className="layout">
			    <Header style={{background:'white',paddingLeft:'100px'}}>
					  <div className={styles.logo}  />
					  <Menu
						theme="light"
						mode="horizontal"
						defaultSelectedKeys={['2']}
						style={{ lineHeight: '64px',padding:'0 40px' }}
					  >
							
							<Menu.Item key="1" style={{ padding:'0 40px' }}><NavLink to='/home/index'><Icon type="home" />首页</NavLink></Menu.Item>
							<Menu.Item key="2" style={{ padding:'0 40px' }}><NavLink to='/home/mypro'><Icon type="database" theme="filled" />我的订单</NavLink></Menu.Item>
							<Menu.Item key="3" style={{ padding:'0 40px' }}><NavLink to='/home/service'><Icon type="smile" theme="filled" />加盟合作</NavLink></Menu.Item>
							<Menu.Item key="4" style={{ padding:'0 40px' }}><NavLink to='/home/question'><Icon type="mobile" theme="filled" />我的客服</NavLink></Menu.Item>
							<Menu.Item key="5" style={{ padding:'0 40px' }}><NavLink to='/home/phone'><span><Icon type="chrome" theme="filled"/>规则中心</span></NavLink></Menu.Item>
							<Menu.Item key="6" className="nav3" style={{marginLeft:'100px'}}><span><Icon type="chrome" theme="filled" spin/>
							<Popover content={content} >
							手机应用
							</Popover></span></Menu.Item>

							
							<Menu.Item key="7"  style={{marginLeft:'40px'}} ><span onClick={this.login.bind(this)}>登录</span></Menu.Item>
							<Menu.Item key="8"  style={{marginLeft:'40px'}} ><span onClick={this.register.bind(this)}>注册</span></Menu.Item>
							<Menu.Item key="9"  style={{marginLeft:'40px'}} ><span onClick={this.Cart.bind(this)}>购物车</span></Menu.Item>
					  </Menu>
			    </Header>
			    <Content style={{ padding: '0 50px' }}>
					  <Breadcrumb style={{ margin: '16px 0',height:80}}>						
							<Breadcrumb.Item>当前位置：</Breadcrumb.Item>
							<Breadcrumb.Item>{this.state.address.name}</Breadcrumb.Item>
							<Breadcrumb.Item>
							<div>
								<span type="primary" onClick={this.showModal}>
								[切换地址]
								</span>
								<Modal
								title="选择城市"
								visible={this.state.visible}
								onOk={this.handleOk}
								onCancel={this.handleCancel}
								>
								
								{
									this.state.index.map((item,i)=>{
										return(
											<dl>
												<dt>{item}</dt>
												<dd>
												{/* {
													this.state.city.map((items,j)=>{
														return(
															<div>{items.name}</div>
														)
													})
												} */}
												</dd>
											</dl>
											
										)
									})
								}
								
								
								</Modal>
							</div>
							</Breadcrumb.Item>							
						</Breadcrumb>

						<Search 
							placeholder="搜索商家，美食..."
							onSearch={value => console.log(value)}
							style={{ width: 200,float:'right',marginTop: -98 }}
						/>

						
						<img src={require('./boy.png')} style={{width:168,height:56,position:'absolute',right:150,top:128}} />
						
					  
					  <div style={{ background: '#fff', padding: 24, minHeight: 680,overflow:'auto' }}>,
					  <Router>

					   <Switch>
							<Route path='/home/service' component={Service}></Route>
							<Route path='/home/index' component={Index}></Route>	
							<Route path='/home/question' component={Question}></Route>
							<Route path='/home/phone' component={Phone}></Route>
							<Route path='/home/mypro' component={Mypro}></Route>
							<Route path='/home/detail' component={Detail}></Route>

							{/* <Redirect to="/index"/> */}
						</Switch>
			  </Router>
					  
					  </div>
			    </Content>
				<img src={img} />
			    <Footer style={{ textAlign: 'center' }}>Ant Design ©2018 Created by Ant UED</Footer>,
				
				{/* <div  style={{width:this.state.width,
									  position:"fixed",right:0,height:580,background:'yellow',top:30,transition:'width 1s'}}>
								<Icon onClick={this.toggle.bind(this)} type="left-square"  
								rotate={this.state.rotate}
								style={{transition:'rotate 1s',fontSize:24,lineHeight:"580px"}}
								/>
								<div style={{position:'absolute',background:'green',top:"0px",width:240,left: 20}}>
									<h3 style={{width:'100%',height:'24px',textAlign:'center',lineHeight:'27px'}}>购物车</h3>
									<div style={{width:'100%',height:'548px',background:'pink'}}></div>
								</div>
				</div> */}
			  </Layout>
		)
	}
	componentDidMount(){
		axios({
			url:'https://elm.cangdu.org/v1/cities?type=guess'
		}).then((data)=>{
			this.setState({address:data.data})
		})
		// api.getCity({type:guess}).then(res=>{
		// 	console.log(res)
		// 	// this.setState({address:data.data})
		// })
		let index=[]
		// let city=[]
		axios({
			
			url:'https://elm.cangdu.org/v1/cities?type=group'
		}).then((data)=>{
			// console.log(data.data)
			for(let i in data.data){
				// console.log(data.data[i])
				
				index.push(i)
				index=index.sort()
				this.setState({index:index})
				
				// city.push(data.data[i])
				// this.setState({city:city})
				// console.log(city)
				// for (let j = 0; j < data.data[i].length; j++) {
					// console.log(data.data[i][j])
				// 	city.push(data.data[i][j])
				// 	this.setState({city:city})
				// }

			}
			// console.log(city)

			// this.setState({address:data.data})
		})
		
	}
	
}