import React,{Component} from 'react'
import { Avatar,Card, Col, Row, Button} from 'antd';
// import api from '../../api/api'
import axios from 'axios'
import style from './Oldorder.module.css'
export default class Person extends Component{
	state={
		list:[],
		lists:[]
	}
	render(){
		return(
			<div style={{ background: '#ECECEC', padding: '30px' }}>
			    <Row gutter={20}>
			      <Col span={9}>
			        <Card title="Card title" bordered={false} style={{textAlign:"center"}}>
					   <span style={{fontSize:20,color:'red',textAlign:"center"}}>你好</span>
			        </Card>
			      </Col>
			      <Col span={5} onClick={this.tap.bind(this)}>
			        <Card title="我的红包" bordered={false} style={{textAlign:"center"}}>
			          <p style={{fontSize:20,color:'red',textAlign:"center"}}>{this.state.list.length}</p>
			        </Card>
			      </Col>
			      <Col span={5}>
			        <Card title="我的金币" bordered={false} style={{textAlign:"center"}}>
					<p style={{fontSize:20,color:'orange',textAlign:"center"}}>4</p>
			        </Card>
			      </Col>
				  <Col span={5}>
				    <Card title="账户余额" bordered={false} style={{textAlign:"center"}}>
					<p style={{fontSize:20,color:'green',textAlign:"center"}}>99999999.00</p>
				    </Card>
				  </Col>
			    </Row>
				<Card size="small" title="我的订单" extra={<a href="#">More</a>} style={{ width: "100%",marginTop:'20px',overflow: "auto",
    height: "321px" }}>
				    <table className={style.tab}>
									{
										this.state.lists.map((item,i)=>{
											return(
												<tr>
													<td><img style={{width:60}} src={'http://elm.cangdu.org/img/'+item.restaurant_image_url}/></td>
													<td>{item.restaurant_name}</td>
													<td>{item.formatted_created_at}</td>
													<td>
													{
														item.basket.group[0].map((items)=>{
															return(
																<span>{items.price}</span>
															)
														})
													}
													</td>
													
													<td>状态</td>
													<td><Button type="primary">订单详情</Button></td>
												</tr>
											)
										})
									}
									
									
								</table>
					  {/* <div style={{ background: '#ECECEC', padding: '30px' }}>
							<Card title="我的订单" bordered={false} style={{ width: "100%", height: "465px",overflow:'hidden',overflow:'auto'}}>
							</Card>
						</div> */}
				    </Card>
				  
			  </div>
		)
	}
	tap(){
		this.props.history.push('/rpacket')
	}
	componentDidMount(){
		
		axios({
			url:'https://elm.cangdu.org/promotion/v2/users/1/hongbaos?limit=20&offset=0',
		}).then((data)=>{
			console.log(data.data)
			this.setState({list:data.data})
		})

		axios({
			url:'https://elm.cangdu.org/bos/orders?offset=0&limit=20'
		}).then((data)=>{
			console.log(data.data)
			this.setState({lists:data.data})
		})
	}
}