import * as API from './index';

// 获取验证码
export default{
	getCaptchas:params=>{
		return API.POST('/v1/captchas',params)
	},
	getLogin:params=>{
		return API.POST('/v2/login',params)
	},
	// 获取所有商铺
	getShopCategory:params=>{
		return API.GET('/shopping/v2/restaurant/category',params)
	},
	// 获取经纬度
	getCity:params=>{
		return API.GET('/v1/cities',params)
	},
	// 根据经纬度获取商铺信息
	getShopList:params=>{
		return API.GET('/shopping/restaurants',params)
	},
	// 获取视频分类
	getShopMenu:params=>{
		return API.GET('/shopping/v2/menu',params)
	},
	Login:params=>{
		return API.GET1('/login.php',params)
	},
	Register:params=>{
		return API.GET1('/reg.php',params)
	}
}